import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from '../StaticInstance';
import AudioManager from "../manager/AudioManager";
import PoolManager from "../manager/PoolManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import LeaderboardItem from "../ui/LeaderboardItem";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { TestManager } from "../api/TestManager";
import { UserController } from "../api/controllers/UserController";
import HistoryItem from "../ui/HistoryItem";
import BottomNavigationPanel from "../ui/BottomNavigationPanel";
import { delay } from "../Utils";

const { ccclass, property } = cc._decorator;

@ccclass
export default class HistoryCombinedLayer extends BaseLayer {

    btnClose: cc.Node = null

    totalWinningPoolLabel: cc.Label = null


    content: cc.Node = null

    onLoad() {
        const tabs_panel = cc.find('tabs_panel', this.node);
        tabs_panel.addComponent(BottomNavigationPanel);

        this.btnClose = cc.find('btn_close', this.node)
        this.content = cc.find('main/scroll_view/view/content', this.node)
        this.totalWinningPoolLabel = cc.find('top/layout/pool_amount', this.node).getComponent(cc.Label);

        this.btnClose.on(cc.Node.EventType.TOUCH_START, this.onCloseClick, this)
    }

    onDestroy() {
        this.btnClose.off(cc.Node.EventType.TOUCH_START, this.onCloseClick, this)
    }

    onEnable() {
        const contentNode = this.content;
        const poolManager = PoolManager.instance;

        UserController.Instance?.getSumPointsLastWinners().then(async (response) => {
           
            const players = response.data;

            players.sort((a, b) => b.points - a.points);

            contentNode.destroyAllChildren();

            for (let i = 0; i < players.length; i++) {
                const player = players[i];
                const item = poolManager.getNode(`history_item`, contentNode);
                const leaderboardItem = item.getComponent(HistoryItem);
                leaderboardItem.init(player.winning_amount, player.User.username ? `@${player.User.username}` : `${player.User.first_name}`, player.points);

                await delay(100)

            }

        }).catch((error) => {
            console.error("Failed to fetch leaderboard:", error);
        });

        UserController.Instance?.getLastWeekPrizePool().then((response) => {
            this.totalWinningPoolLabel.string = response.data.total_winning_amount.toFixed(2);
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });

    }

    onCloseClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HISTORY_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED)
    }
}
