import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from '../StaticInstance';
import AudioManager from "../manager/AudioManager";
import PoolManager from "../manager/PoolManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import LeaderboardItem from "../ui/LeaderboardItem";
import { UserController } from "../api/controllers/UserController";
import { delay, getRemainingTimeToDateMs, getRemainingTimeToDateSeconds, setDesignResolution } from "../Utils";
import { WINNING_AMOUNT_ADDITIONAL_PRIZE, WINNING_AMOUNT_ADDITIONAL_PRIZES, WINNING_PERCENT_1st_PLACE as WINNING_PERCENT_1_PLACE, WINNING_PERCENT_2st_PLACE as WINNING_PERCENT_2_PLACE, WINNING_PERCENT_3st_PLACE as WINNING_PERCENT_3_PLACE, WINNING_PERCENT_4st_PLACE as WINNING_PERCENT_4_PLACE, WINNING_PERCENT_5st_PLACE as WINNING_PERCENT_5_PLACE, getPrizeAmount } from "../constants/prize-pool.const";
import BottomNavigationPanel from "../ui/BottomNavigationPanel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ClubLayer extends BaseLayer {

    youItem: LeaderboardItem = null
    blogerItem: LeaderboardItem = null

    info: cc.Node = null
    content: cc.Node = null

    btnWinners: cc.Node = null
    btnCloseInfo: cc.Node = null

    blogger_name: cc.Label = null;
    club_members: cc.Label = null;


    tournament_bar: cc.Node = null
    tournament_bar_base: cc.Node = null
    tournament_bar_fill: cc.Sprite = null;
    tournament_bar_time_left: cc.Label = null;

    info_tournament_time_left: cc.Label = null;
    info_tournament_prize: cc.Label = null;

    onLoad() {
        this.info = cc.find('info', this.node)
        this.content = cc.find('main/scroll_view/view/content', this.node)
        this.youItem = cc.find('main/leaderboard_item_you', this.node).getComponent(LeaderboardItem);
        this.blogerItem = cc.find('main/leaderboard_item_blogger', this.node).getComponent(LeaderboardItem);

        const tabs_panel = cc.find('tabs_panel', this.node);
        tabs_panel.addComponent(BottomNavigationPanel);

        this.btnWinners = cc.find('main/top_win/btn_winners', this.node)
        this.btnCloseInfo = cc.find('panel/btn_close', this.info)

        this.blogger_name = cc.find('blogger_nickname', this.node).getComponent(cc.Label)
        this.club_members = cc.find('users/label', this.node).getComponent(cc.Label)

        //week task bar
        this.tournament_bar = cc.find('main/tasks_weekly', this.node)
        this.tournament_bar_base = cc.find('base', this.tournament_bar)
        this.tournament_bar_fill = cc.find('fill_bar/fill', this.tournament_bar_base).getComponent(cc.Sprite)
        this.tournament_bar_time_left = cc.find('time_left_layout/label', this.tournament_bar_base).getComponent(cc.Label)

        this.info_tournament_time_left = cc.find('panel/bottom/time_text', this.info).getComponent(cc.Label)
        this.info_tournament_prize = cc.find('panel/used_hints/prize_layout/amount', this.info).getComponent(cc.Label)


        this.btnWinners.on(cc.Node.EventType.TOUCH_START, this.onWinnersClick, this)
        this.tournament_bar.on(cc.Node.EventType.TOUCH_START, this.onInfoClick, this)
        this.btnCloseInfo.on(cc.Node.EventType.TOUCH_START, this.onInfoCloseClick, this)

        this.youItem.node.active = false;

        this.club_members.string = "111"
        this.info_tournament_prize.string = "111111"
        this.blogger_name.string = "@test"
    }

    onDestroy() {
        this.btnWinners.off(cc.Node.EventType.TOUCH_START, this.onWinnersClick, this)
        this.tournament_bar.off(cc.Node.EventType.TOUCH_START, this.onInfoClick, this)
        this.btnCloseInfo.off(cc.Node.EventType.TOUCH_START, this.onInfoCloseClick, this)
    }

    onEnable() {
        const youItem = this.youItem;
        const contentNode = this.content;
        const poolManager = PoolManager.instance;

        const winningAmountTotal = DataManager.instance.prizePoolWinningAmount;
        const winningAmount = (winningAmountTotal - WINNING_AMOUNT_ADDITIONAL_PRIZES) / 2;
      

        this.blogerItem.initBlogger(`@test`, 11111);

        UserController.Instance?.getLeaderboard().then(async (response) => {
            

            if(response.user_position){
                const you = response.user_position;
                const youPrize = getPrizeAmount(response.user_position.position - 1, winningAmount);
                youItem.init(you.position, `@${you.username}`, you.points, youPrize);
                //youItem.node.active = true;
                const players = response.leaderboard;
                contentNode.destroyAllChildren();
    
                for (let i = 0; i < players.length; i++) {
                    const prize = getPrizeAmount(i, winningAmount);
                    const player = players[i];
                    const item = player.user_id == you.user_id ? poolManager.getNode(`leaderboard_item_you`, contentNode) : poolManager.getNode(`leaderboard_item`, contentNode);
                    const leaderboardItem = item.getComponent(LeaderboardItem);
                    const name = (player.username && player.username != "" && player.username != null) ? `@${player.username}` : `${player.first_name}`;
                    leaderboardItem.init(player.position, name, player.points, prize);

                    await delay(100)

                }
            }

            else{
                const players = response.leaderboard;
                contentNode.destroyAllChildren();
    
                for (let i = 0; i < players.length; i++) {
                    const prize = getPrizeAmount(i, winningAmount);
                    const player = players[i];
                    const item = poolManager.getNode(`leaderboard_item`, contentNode);
                    const leaderboardItem = item.getComponent(LeaderboardItem);
                    const name = (player.username && player.username != "" && player.username != null) ? `@${player.username}` : `${player.first_name}`;
                    leaderboardItem.init(player.position, name, player.points, prize);

                    await delay(100)
                }
            }
            
            setDesignResolution();

        }).catch((error) => {
            console.error("Failed to fetch leaderboard:", error);
        });


        if(DataManager.instance.nextMonday){
            this.setTournamentEndFill(getRemainingTimeToDateMs(DataManager.instance.nextMonday), 604800000);
        } 
    }

    onDisable() { }

    protected update(dt: number): void {
        if(DataManager.instance.nextMonday){
           this.info_tournament_time_left.string = this.tournament_bar_time_left.string = getRemainingTimeToDateSeconds(DataManager.instance.nextMonday);


           //this.setTournamentEndFill(getRemainingTimeToDateMs(DataManager.instance.nextMonday), 604800000);

        } 
    }

    setTournamentEndFill(played : number, target : number){
        const fillRange = played / target;
        this.tournament_bar_fill.fillRange = fillRange;
    }

    onWinnersClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.CLUB, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.CLUB_HISTORY)
    }

    onInfoClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.info.active = true;
    }

    onInfoCloseClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.info.active = false;
    }
    
}
