import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BottomNavigationPanel extends cc.Component {

    btnMain: cc.Node = null
    btnLeaderboard: cc.Node = null
    btnTasks: cc.Node = null
    btnProfile: cc.Node = null
    btnOutput: cc.Node = null
    btnClub: cc.Node = null
    
    onLoad() {
        this.btnMain = cc.find('btn_main', this.node)
        this.btnLeaderboard = cc.find('btn_leader', this.node)
        this.btnTasks = cc.find('btn_tasks', this.node)
        this.btnProfile = cc.find('btn_profile', this.node)
        this.btnOutput = cc.find('btn_out', this.node)
        this.btnClub = cc.find('btn_club', this.node)


        this.btnMain.on(cc.Node.EventType.TOUCH_START, this.onMainClick, this)
        this.btnLeaderboard.on(cc.Node.EventType.TOUCH_START, this.onLeaderboardClick, this)
        this.btnTasks.on(cc.Node.EventType.TOUCH_START, this.onTasksClick, this)
        this.btnProfile.on(cc.Node.EventType.TOUCH_START, this.onProfileClick, this)
        this.btnOutput.on(cc.Node.EventType.TOUCH_START, this.onOutputClick, this)
        this.btnClub.on(cc.Node.EventType.TOUCH_START, this.onClubClick, this)

        this.btnOutput.active = true;
    }

    onDestroy() {
        //cc.Node.EventType.MOUSE_DOWN
        this.btnMain.off(cc.Node.EventType.TOUCH_START, this.onMainClick, this)
        this.btnLeaderboard.off(cc.Node.EventType.TOUCH_START, this.onLeaderboardClick, this)
        this.btnTasks.off(cc.Node.EventType.TOUCH_START, this.onTasksClick, this)
        this.btnProfile.off(cc.Node.EventType.TOUCH_START, this.onProfileClick, this)
        this.btnOutput.off(cc.Node.EventType.TOUCH_START, this.onOutputClick, this)
        this.btnClub.off(cc.Node.EventType.TOUCH_START, this.onClubClick, this)
    }

    onMainClick() {
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU)
    }

    onLeaderboardClick() {
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTasksClick() {
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS)
    }

    onProfileClick() {
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE)
    }

    onOutputClick() {
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.OUTPUT)
    }

    onClubClick(){
        this.hideAllPanels();
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.CLUB)
    }

    hideAllPanels(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HISTORY, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HISTORY_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.OUTPUT, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.CLUB, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.CLUB_HISTORY, false)

    }
}
