export const WINNING_AMOUNT_ADDITIONAL_PLACES: number = 95;
export const WINNING_AMOUNT_ADDITIONAL_PRIZE: number = 50;
export const WINNING_AMOUNT_ADDITIONAL_PRIZES: number = WINNING_AMOUNT_ADDITIONAL_PRIZE * WINNING_AMOUNT_ADDITIONAL_PLACES * 2;


export const WINNING_PERCENT_1st_PLACE: number = 0.35;
export const WINNING_PERCENT_2st_PLACE: number = 0.20;
export const WINNING_PERCENT_3st_PLACE: number = 0.15;
export const WINNING_PERCENT_4st_PLACE: number = 0.10;
export const WINNING_PERCENT_5st_PLACE: number = 0.05;


export function getPrizeAmount(index: number, winningAmount: number): number {
    let prizeAmount: number = 0;

    if(index > 99)
        return 0;

    switch (index) {
        case 0:
            prizeAmount = winningAmount * WINNING_PERCENT_1st_PLACE;
            break;
        case 1:
            prizeAmount = winningAmount * WINNING_PERCENT_2st_PLACE;
            break;
        case 2:
            prizeAmount = winningAmount * WINNING_PERCENT_3st_PLACE;
            break;
        case 3:
            prizeAmount = winningAmount * WINNING_PERCENT_4st_PLACE;
            break;
        case 4:
            prizeAmount = winningAmount * WINNING_PERCENT_5st_PLACE;
            break;
        default:
            prizeAmount = WINNING_AMOUNT_ADDITIONAL_PRIZE;
            break;
    }

    return Math.max(prizeAmount, 0);
}