import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from '../StaticInstance';
import AudioManager from "../manager/AudioManager";
import PoolManager from "../manager/PoolManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import LeaderboardItem from "../ui/LeaderboardItem";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { TestManager } from "../api/TestManager";
import { UserController } from "../api/controllers/UserController";
import { delay, getRemainingTimeToDateSeconds, setDesignResolution } from "../Utils";
import { WINNING_AMOUNT_ADDITIONAL_PRIZES, getPrizeAmount } from "../constants/prize-pool.const";
import BottomNavigationPanel from "../ui/BottomNavigationPanel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LeaderboardCombinedLayer extends BaseLayer {

    youItem: LeaderboardItem = null

    btnTabBest: cc.Node = null
    btnTabCombine: cc.Node = null

    btnWinners: cc.Node = null
    btnInfo: cc.Node = null
    btnCloseInfo: cc.Node = null
    btnFindMe: cc.Node = null
    
    info: cc.Node = null
    

    content: cc.Node = null

    taskFill: cc.Sprite = null;
    taskAmountLabel: cc.Label = null;
    taskTimeLeft: cc.Label = null;

    onLoad() {
        const tabs_panel = cc.find('tabs_panel', this.node);
        tabs_panel.addComponent(BottomNavigationPanel);

        this.info = cc.find('info', this.node)
        this.content = cc.find('main/scroll_view/view/content', this.node)
        this.youItem = cc.find('main/leaderboard_item_you', this.node).getComponent(LeaderboardItem);
    
        this.btnFindMe = cc.find('btn_find_me', this.node)
        this.btnTabBest = cc.find('btn_tab_best', this.node)
        this.btnTabCombine = cc.find('btn_tab_combine', this.node)

        this.btnWinners = cc.find('main/top_win/btn_winners', this.node)
        this.btnInfo = cc.find('main/top_win/btn_info', this.node)
        this.btnCloseInfo = cc.find('panel/btn_leaderboard', this.info)

        this.taskFill = cc.find('panel/games_weekly/fill_bar/fill', this.info).getComponent(cc.Sprite)
        this.taskAmountLabel = cc.find('panel/games_weekly/amount', this.info).getComponent(cc.Label)
        this.taskTimeLeft = cc.find('panel/bottom/time_text', this.info).getComponent(cc.Label)

        this.btnTabBest.on(cc.Node.EventType.TOUCH_START, this.onTabBestClick, this)
        //this.btnTabCombine.on(cc.Node.EventType.TOUCH_START, this.onTabCombineClick, this)

        this.btnFindMe.on(cc.Node.EventType.TOUCH_START, this.onFindMeClick, this)
        this.btnWinners.on(cc.Node.EventType.TOUCH_START, this.onWinnersClick, this)
        this.btnInfo.on(cc.Node.EventType.TOUCH_START, this.onInfoClick, this)
        this.btnCloseInfo.on(cc.Node.EventType.TOUCH_START, this.onInfoCloseClick, this)
        
        this.youItem.node.active = false;
    }

    onDestroy() {
        this.btnTabBest.off(cc.Node.EventType.TOUCH_START, this.onTabBestClick, this)
        //this.btnTabCombine.off(cc.Node.EventType.TOUCH_START, this.onTabCombineClick, this)

        this.btnFindMe.off(cc.Node.EventType.TOUCH_START, this.onFindMeClick, this)
        this.btnWinners.off(cc.Node.EventType.TOUCH_START, this.onWinnersClick, this)
        this.btnInfo.off(cc.Node.EventType.TOUCH_START, this.onInfoClick, this)
        this.btnCloseInfo.off(cc.Node.EventType.TOUCH_START, this.onInfoCloseClick, this)
    }

    onEnable() {
        const youItem = this.youItem;
        const contentNode = this.content;
        const poolManager = PoolManager.instance;

        const winningAmountTotal = DataManager.instance.prizePoolWinningAmount;
        const winningAmount = (winningAmountTotal - WINNING_AMOUNT_ADDITIONAL_PRIZES) / 2;

        UserController.Instance?.getSumPointsLeaderboard().then(async (response) => {
            

            if(response.user_position){
                const you = response.user_position;
                const youPrize = getPrizeAmount(response.user_position.position - 1, winningAmount);
                youItem.init(you.position, `@${you.username}`, you.points, youPrize);
                youItem.node.active = true;
                const players = response.leaderboard;
                contentNode.destroyAllChildren();
    
                for (let i = 0; i < players.length; i++) {
                    const prize = getPrizeAmount(i, winningAmount);
                    const player = players[i];
                    const item = player.user_id == you.user_id ? poolManager.getNode(`leaderboard_item_you`, contentNode) : poolManager.getNode(`leaderboard_item`, contentNode);
                    const leaderboardItem = item.getComponent(LeaderboardItem);
                    const name = (player.username && player.username != "" && player.username != null) ? `@${player.username}` : `${player.first_name}`;
                    leaderboardItem.init(player.position, name, player.points, prize);

                    await delay(100)

                }
            }

            else{
                const players = response.leaderboard;
                contentNode.destroyAllChildren();
    
                for (let i = 0; i < players.length; i++) {
                    const prize = getPrizeAmount(i, winningAmount);
                    const player = players[i];
                    const item = poolManager.getNode(`leaderboard_item`, contentNode);
                    const leaderboardItem = item.getComponent(LeaderboardItem);
                    const name = (player.username && player.username != "" && player.username != null) ? `@${player.username}` : `${player.first_name}`;
                    leaderboardItem.init(player.position, name, player.points, prize);

                    await delay(100)
                }
            }
            
            UserController.Instance.getGames250_count().then(response => {
                console.log(response);
                this.setGamesWeeklyAmount(response.sum_points, 5000);
            });

            setDesignResolution();

        }).catch((error) => {
            console.error("Failed to fetch leaderboard:", error);
        });

    }

    onDisable() { }

    protected update(dt: number): void {
        if(DataManager.instance.nextMonday){
            this.taskTimeLeft.string = getRemainingTimeToDateSeconds(DataManager.instance.nextMonday);
        } 
    }

    setGamesWeeklyAmount(played : number, target : number){
        const fillRange = played / target;
        this.taskAmountLabel.string = `${played}/${target}`
        this.taskFill.fillRange = fillRange;
    }

    onTabBestClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD)
    }

    onTabCombineClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED)
    }

    onFindMeClick() { AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK) }

    onWinnersClick() { 
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.MENU, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD_COMBINED, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.LEADERBOARD, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.TASKS, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.HISTORY_COMBINED)
    }

    onInfoClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.info.active = true;
    }

    onInfoCloseClick() {
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.info.active = false;
    }
    
}
