import { ENUM_AUDIO_CLIP, ENUM_GAME_MODE, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import AvatarLoader  from "../utils/AvatarLoader";
import {TelegramWebApp} from "../telegram/TelegramMiniApp";
import { UserController } from "../api/controllers/UserController";
import BottomNavigationPanel from "../ui/BottomNavigationPanel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ProfileLayer extends BaseLayer {

    avatarNode: cc.Node = null
    nameNode: cc.Node = null

    btnSettings: cc.Node = null
    btnTutorial: cc.Node = null
    btnTryGame: cc.Node = null
    btnSupport: cc.Node = null
    btnPrivacy: cc.Node = null
    btnPartership: cc.Node = null
    btnGuidebookRules: cc.Node = null

    //disable ads
    btnOpenDisableAdsPopup: cc.Node = null
    btnCloseDisableAdsPopup: cc.Node = null
    btnBuyDisableAds: cc.Node = null
    btnCloseSuccessAdsPopup: cc.Node = null

    disableAdsPopup: cc.Node = null
    successAdsPopup: cc.Node = null

    onLoad() {
        const tabs_panel = cc.find('tabs_panel', this.node);
        tabs_panel.addComponent(BottomNavigationPanel);

        this.avatarNode = cc.find('main/panel_user/icon_back/icon', this.node)
        this.nameNode = cc.find('main/panel_user/username', this.node)

        this.btnSettings = cc.find('main/btn_settings', this.node)
        this.btnTutorial = cc.find('main/btns_group_1/btn_tutorial', this.node)
        this.btnTryGame = cc.find('main/btns_group_1/btn_try_game', this.node)
        this.btnGuidebookRules = cc.find('main/btns_group_2/btn_guidebook', this.node)
        this.btnSupport = cc.find('main/btns_group_2/btn_support', this.node)
        this.btnPrivacy = cc.find('main/btns_group_2/btn_policy', this.node)
        this.btnPartership = cc.find('main/btn_partership', this.node)
        this.btnOpenDisableAdsPopup = cc.find('main/btn_no_ads', this.node)

        
        this.disableAdsPopup = cc.find('disable_ads_popup', this.node)
        this.btnCloseDisableAdsPopup = cc.find('disable_ads_popup/panel/btn_close', this.node)
        this.btnBuyDisableAds = cc.find('disable_ads_popup/panel/btn_yes', this.node)

        this.successAdsPopup = cc.find('success_ads_popup', this.node)
        this.btnCloseSuccessAdsPopup = cc.find('success_ads_popup/panel/btn_yes', this.node)

        this.btnOpenDisableAdsPopup.on(cc.Node.EventType.TOUCH_START, this.onDisableAdsClickOpen, this)
        this.btnCloseDisableAdsPopup.on(cc.Node.EventType.TOUCH_START, this.onDisableAdsClickClose, this)
        this.btnBuyDisableAds.on(cc.Node.EventType.TOUCH_START, this.onBuyDisableAdsClick, this)
        this.btnCloseSuccessAdsPopup.on(cc.Node.EventType.TOUCH_START, this.onSuccessAdsClickClose, this)

        this.btnSettings.on(cc.Node.EventType.TOUCH_START, this.onSettingsClick, this)
        this.btnTutorial.on(cc.Node.EventType.TOUCH_START, this.onTutorialClick, this)
        this.btnTryGame.on(cc.Node.EventType.TOUCH_START, this.onTryGameClick, this)
        this.btnSupport.on(cc.Node.EventType.TOUCH_START, this.onSupportClick, this)
        this.btnPrivacy.on(cc.Node.EventType.TOUCH_START, this.onPrivacyClick, this)
        this.btnPartership.on(cc.Node.EventType.TOUCH_START, this.onPartnershipClick, this)
        this.btnGuidebookRules.on(cc.Node.EventType.TOUCH_START, this.onGuidebookClick, this)

        //var user = TelegramWebApp.Instance.getTelegramUser();
        //this.nameNode.getComponent(cc.Label).string = user.username;
        //AvatarLoader.loadAvatar(user.photo_url, this.avatarNode);
    }

    onDestroy() {

        this.btnOpenDisableAdsPopup.off(cc.Node.EventType.TOUCH_START, this.onDisableAdsClickOpen, this)
        this.btnCloseDisableAdsPopup.off(cc.Node.EventType.TOUCH_START, this.onDisableAdsClickClose, this)
        this.btnBuyDisableAds.off(cc.Node.EventType.TOUCH_START, this.onBuyDisableAdsClick, this)
        this.btnCloseSuccessAdsPopup.off(cc.Node.EventType.TOUCH_START, this.onSuccessAdsClickClose, this)


        this.btnSettings.off(cc.Node.EventType.TOUCH_START, this.onSettingsClick, this)
        this.btnTutorial.off(cc.Node.EventType.TOUCH_START, this.onTutorialClick, this)
        this.btnTryGame.off(cc.Node.EventType.TOUCH_START, this.onTryGameClick, this)
        this.btnSupport.off(cc.Node.EventType.TOUCH_START, this.onSupportClick, this)
        this.btnPrivacy.off(cc.Node.EventType.TOUCH_START, this.onPrivacyClick, this)
        this.btnPartership.off(cc.Node.EventType.TOUCH_START, this.onPartnershipClick, this)
        this.btnGuidebookRules.off(cc.Node.EventType.TOUCH_START, this.onGuidebookClick, this)

    }

    onEnable() { 
         UserController.Instance?.getUser().then((response) => {
            const data = response.data;
            const name = (data.username && data.username != "" && data.username != null) ? `@${data.username}` : `${data.first_name}`;
            this.nameNode.getComponent(cc.Label).string = name;
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });
    }

    onSettingsClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.PROFILE, false)
        StaticInstance.uiManager.toggle(ENUM_UI_TYPE.SETTING)
    }

    onTutorialClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
    }

    onTryGameClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
    }

    onSupportClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        TelegramWebApp.Instance?.openTelegramLinkSupport();
    }

    onPrivacyClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        TelegramWebApp.Instance?.openLinkPrivacy();
    }

    onPartnershipClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        TelegramWebApp.Instance?.openLinkPartnership();
    }

    onGuidebookClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        TelegramWebApp.Instance?.openLinkGuidebook();
    }

    onDisableAdsClickOpen(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        UserController.Instance?.getUser().then((response) => {
            if(!response.data.is_premium){
                this.openDisableAdsPopup();
            }
            else{
                this.successAdsPopup.active = true;
            }
        }).catch((error) => {
            console.error("Failed to fetch user data:", error);
        });

    }

    onDisableAdsClickClose(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.disableAdsPopup.active = false;
    }

    onSuccessAdsClickClose(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.successAdsPopup.active = false;
    }

    openDisableAdsPopup(){
        this.disableAdsPopup.active = true;
    }

    onBuyDisableAdsClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)

        UserController.Instance?.postInvoicePremium().then((response) => {
            console.log(response.invoice_link);

            TelegramWebApp.Instance.openInvoice(response.invoice_link,  (status, url) =>{

                if(status === 'paid'){
                    this.disableAdsPopup.active = false;
                    this.successAdsPopup.active = true;
                }

                console.log("invoice callback");
                console.log(status);
                console.log(url);
            });

        }).catch((error) => {
            console.error("Failed transaction:", error);
        });
    }
}
