import { ENUM_AUDIO_CLIP, ENUM_UI_TYPE } from "../Enum";
import { StaticInstance } from './../StaticInstance';
import AudioManager from "../manager/AudioManager";
import BaseLayer from "./Baselayer";
import DataManager from "../manager/DataManager";
import BottomNavigationPanel from "../ui/BottomNavigationPanel";
import { TelegramWebApp } from "../telegram/TelegramMiniApp";
import { UserController } from "../api/controllers/UserController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class OutputLayer extends BaseLayer {

    labelCoinsAmount: cc.Label = null;
    
    btn_next: cc.Node = null
    btn_out: cc.Node = null
    btn_out_not_active: cc.Node = null
    btn_close_out: cc.Node = null
    
    panel_main: cc.Node = null
    panel_out: cc.Node = null

    edit_box: cc.EditBox = null;
   

    public static block_resize: boolean = false;


    onLoad() {
        const tabs_panel = cc.find('tabs_panel', this.node);
        tabs_panel.addComponent(BottomNavigationPanel);
        
        this.panel_main = cc.find('main', this.node)
        this.panel_out = cc.find('out', this.node)

        this.labelCoinsAmount = cc.find('coin_balance/coins_amount', this.node).getComponent(cc.Label)

        this.edit_box = cc.find('edit_box', this.panel_out).getComponent(cc.EditBox)

        this.btn_close_out = cc.find('btn_close', this.node)
        this.btn_next = cc.find('btn_start', this.panel_main)
        this.btn_out = cc.find('btn_start', this.panel_out)
        this.btn_out_not_active = cc.find('btn_start_2', this.panel_out)

        this.btn_close_out.on(cc.Node.EventType.TOUCH_START, this.onCloseOutClick, this)
        this.btn_next.on(cc.Node.EventType.TOUCH_START, this.onNextClick, this)
        this.btn_out.on(cc.Node.EventType.TOUCH_START, this.onOutClick, this)

        this.edit_box.node.on('editing-did-began',  this.onEditBegan, this);
        this.edit_box.node.on('editing-did-ended', this.onEditEnded, this);
        this.edit_box.node.on('text-changes', this.validateInput, this);
    }


    onDestroy() {

        this.btn_close_out.off(cc.Node.EventType.TOUCH_START, this.onCloseOutClick, this)
        this.btn_next.off(cc.Node.EventType.TOUCH_START, this.onNextClick, this)
        this.btn_out.off(cc.Node.EventType.TOUCH_START, this.onOutClick, this)

        this.edit_box.node.off('editing-did-began',  this.onEditBegan, this);
        this.edit_box.node.off('editing-did-ended', this.onEditEnded, this);
        this.edit_box.node.off('text-changes', this.validateInput, this);
    }

    onEnable() {
        OutputLayer.block_resize = false;

        if(DataManager.instance.userData){
            this.edit_box.string = this.labelCoinsAmount.string = DataManager.instance.userData.balance_stars.toString();
        }

        if(this.panel_main){
            this.panel_main.active = true;
        }

        if(this.panel_out){
            this.panel_out.active = false;
            this.btn_close_out.active = false;
        }
        
    }

    onCloseOutClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.panel_main.active = true;
        this.panel_out.active = false;
        this.btn_close_out.active = false;
        OutputLayer.block_resize = false;
    }

    onNextClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        this.panel_main.active = false;
        this.panel_out.active = true;
        this.btn_close_out.active = true;
        OutputLayer.block_resize = true;

        //this.edit_box.adjustPan

        //cc.view.resizeWithBrowserSize(false);
    }

    onOutClick(){
        AudioManager.instance.playSound(ENUM_AUDIO_CLIP.CLICK)
        //todo

        const inputStr = this.edit_box.string.trim();
        let amount = parseInt(inputStr, 10);

        const max = DataManager.instance.userData.balance_stars;

        //      0   0
        if (isNaN(amount) || amount < 0) {
            amount = 0;
        }
        //        
        else if (amount > max) {
            amount = max;
        }

        //backend request
        UserController.Instance?.postWithdrawStars(amount).then((response) => {
            console.error("Success withdraw", response);
            //open wallet app
            TelegramWebApp.Instance.openTelegramLinkBank();
        }).catch((error) => {
            console.error("Failed to withdraw stars:", error);
        });       
    }

    validateInput(){

        setTimeout(() => {
            window.scrollTo(0, 0);
        }, 200);

        const inputStr = this.edit_box.string.trim();
        const value = parseInt(inputStr, 10);

        const max = DataManager.instance.userData.balance_stars;

        //      0   0
        if (isNaN(value) || value < 0) {
            this.edit_box.string = '0';
        }
        //        
        else if (value > max) {
            this.edit_box.string = max.toString();
        }
        //    

        const canOut = value >= 500;
        this.btn_out.active = canOut;
        this.btn_out_not_active.active = !canOut;
    }

    onEditEnded(){
        this.validateInput();

        document.body.removeEventListener('touchmove', preventScroll);
    }

    onEditBegan(){
        document.body.addEventListener('touchmove', preventScroll, { passive: false });

        setTimeout(() => {
            window.scrollTo(0, 0);
        }, 200);
    }

   
}


var preventScroll = function (e) {
    e.preventDefault();
};